import Vue from 'vue'
import confirm from './confirm.vue'

const confirmBox = Vue.extend(confirm)

//  element-ui 
/**
 *  this.$myConfirm(content, options) 
 * @param {String} content 
 * @param {Object} options confirm.vue data
 * @returns 
 */
confirm.install = (content = '', options) => {
  // title  content 
  if (!options) {
    options = {
      content: content,
    }

  } else if (typeof options === 'object') {
    options = Object.assign(
      {
        content: content,
      },
      options
    )
  }

  // console.log(options)`

  let instance = new confirmBox({
    data: options,
  }).$mount()

  //  $mount()  elementOrSelector  (vue)  DOM API 
  document.body.appendChild(instance.$el)

  return instance.showConfirm()
}

export default confirm.install
